<template>
    <nav>
        <div class="nav-icon" @click = "openNav" :class="{open: navOpen}">
            <div></div>
        </div>
        <div class="side-bar-menu" v-if="navOpen" :class="{animate__animated: navAnim, animate__fadeIn: navAnim}">
            <a href="#" class="side-bar-menu-item"><i class="fas fa-home"></i></a>
            <a href="#" class="side-bar-menu-item"><i class="fas fa-info-circle"></i></a>
            <a href="#" class="side-bar-menu-item"><i class="fas fa-briefcase"></i></a>
            <a href="#" class="side-bar-menu-item"><i class="fas fa-envelope"></i></a>
        </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            navOpen: false,
            navAnim: false,

        }
    },
    methods: {
        openNav() {
            if(!this.navOpen && !this.navAnim) {
                this.navOpen = true;
                this.navAnim = true;
            } else if(this.navOpen && this.navAnim){
                this.navOpen = false;
                this.navAnim = false;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../scss/_variables.scss";

.animate__animated {
    animation-delay: 0;
}

.side-bar-menu {
    background-color: #fff;
    padding: 0 2rem;
    position: fixed;
    right: 0;
    top: 7rem;
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    border-radius: 3px;
    box-shadow: 1rem 1rem 4rem rgba(0, 0, 0, 0.15);
        &-item {
            margin: 2.5rem 0;
            color: $color-primary;
            font-size: 1.35rem;
            transition: all 300ms;
            &:hover {
                color: $color-highlight;
                transition: all 300ms;
            };
        }
    }

.nav-icon {
    position: fixed;
    top: 1.4rem;
    right: 8rem;
    margin: 1em;
    width: 1.5rem;
    cursor: pointer;
}

.nav-icon:after,
.nav-icon:before,
.nav-icon div {
    background-color: $color-primary;
    border-radius: 3px;
    content: '';
    display: block;
    height: .2rem;
    margin: 5px 0;
    transition: all .2s ease-in-out;
}


  .nav-icon.open:before {
    transform: translateY(8px) rotate(135deg);
}

  .nav-icon.open:after {
    transform: translateY(-9px) rotate(-135deg);
}

  .nav-icon.open div {
    transform: scale(0);
}


</style>